@mixin Asans($style: light) {
    font-family: 'Alegreya Sans', Helvetica, sans-serif;

    @if $style==light {
        font-weight: 300;
    }

    @if $style==medium {
        font-weight: 500;
    }

    @if $style==bold {
        font-weight: bold;
    }
}

@mixin MuseoSansCyrl {
    font-family: 'MuseoSansCyrl';
    font-weight: 500;
}